
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }

}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0px;
}
#mainWrap{
  width: 100%;
  height: 100vh;
  background-color: #F3E5D0;
  display: flex;
  justify-content: center;
  align-items: center;
 
}

#main{width:365px;
  height:560px;
  background-color: #FFF4CF;
  margin: auto; 
  display: flex;  flex-direction: column;
  border-radius: 20px;
}
  #searchWrap{
    margin: auto;
    width: 280px;height: 35px;
background-color: #B4B182;
  border-radius:10px;   
  color: #D0D0D0;
  padding: 5px;
  padding-left: 10px;
  }
  #Xbutton{
    background-color: #B4B182;
  }
  #searchWrap>input{
    margin-left: 10px;
    margin: auto;
background-color: #B4B182;
  font-size: 13px;
  color: #000;
  padding: 5px;
  padding-left: 10px;
  }
  
   #notelistWrap{
 width: 285px;height: 375px;
 margin: auto;
border-radius:10px;   
overflow-y: auto;
}
#notelistWrap::-webkit-scrollbar {
  width: 10px;
 

}
#notelistWrap::-webkit-scrollbar-thumb {
  background-color: grey;
  border-radius: 20px;
}
#notelistWrap::-webkit-scrollbar-track {
  background-color: #fff;
  border-radius: 20px;
}
#Notelists{width: 265px;height: 60px;
  background-color: #B99777;
  margin: auto;
  text-align: left;
  border-radius: 10px;
  margin-top: 10px;
  }

.NoteTitle{
  width: 100%;
  font-size: 18px;
  color:#fff;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}
.NoteDate{
  text-align: left;
  width: 49%;
font-size: 13px;
color: #D0D0D0;


}
#delbutton{
margin-left: 10px;
margin-right: 5px;
}

li{list-style: none;padding: 5px;}


 #maintitle{
  font-size: 20px;
  width: 150px;
font-weight: bold;
 }
 a{
  color: #faa526;
  text-decoration: none;
 }
 #title{
  margin-left: 90px;
 }
 #Modal{
 position: absolute;
  width: 270px;
  height: 200px;
color: red;
border: 1px solid;
background-color: #fff;
text-align: center;
display: flex;
justify-content: center;
align-items: center;

 }
 #Modal>button{
  margin: auto;
 }